const app = getApp();
import helper from '../common/helper.js';
import MescrollMixin from "@/components/mescroll-uni/mescroll-mixins.js";
export default {
	mixins: [MescrollMixin], // 使用mixin
	data() {
		return {
			list: [],
			page: '1',
			page_size: '10',
			boardId: '0',
			imgUrl: '',
			bank_id: '0',
			child_id: '0',
			vip: '',
			isShow: true,
			isShow1: false,
			downOption: {
				auto: false //是否在初始化后,自动执行downCallback; 默认true
			},
			upOption: {
				page: {
					num: 0, // 当前页码,默认0,回调之前会加1,即callback(page)会从1开始
					size: 10 // 每页数据的数量,默认10
				},
				noMoreSize: 1, // 配置列表的总数量要大于等于5条才显示'-- END --'的提示
				empty: {
					tip: '暂无相关数据'
				},
				textNoMore: '没有更多数据了'
			}
		};
	},
	onShow(e) {
		// console.log(e)
		// this.board_id == e
	},
	onLoad(e) {
		console.log(e,'*******************');
		console.log(e.bankId);
		this.imgUrl = helper.imgUrl;
		console.log(this.imgUrl);
		this.boardId = e.id || 0;

		this.bank_id = e.bankId || 0;
		this.child_id = e.childId || 0;
		// this.getList();
		uni.setNavigationBarTitle({
			title: e.name
		});
	},
	onLaunch() {},
	methods: {
		/*下拉刷新的回调 */
		downCallback() {
			this.mescroll.resetUpScroll()
		},
		 // 上拉回调 page = {num:1, size:10}; num:当前页 ,默认从1开始; size:每页数据条数,默认10
		upCallback (page) {
			this.getList(page)
		},
		getList(page) {
			console.log(page,'-----------------------')
			let pageNum = page.num; // 页码, 默认从1开始
			let pageSize = page.size; // 页长, 默认每页10条
			uni.request({
				url: `${helper.requestUrl}/posts/board-posts`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				data: {
					board_id: this.boardId,
					bank_id: this.bank_id,
					child_id: this.child_id,
					page_size: pageSize,
					page: pageNum
				},
				success: res => {
					res = helper.null2str(res);
					console.log(res);
					if (res.data.status_code == 200) {
						let curPageData = res.data.data
						let totalPage = res.data.total
						if(page.num == 1) this.list = []; 
						this.list = this.list.concat(curPageData);
						this.mescroll.endByPage(curPageData.length, totalPage); 
					} else if (res.data.status_code == 202) {
						this.vip = res.data.message;
						this.isShow1 = true;
						this.isShow = false;
					} else {
						uni.showToast({
							title: res.data.message
						});
					}
				}
			});
		},
		// 跳转详情
		goDetail(e) {
			uni.navigateTo({
				url: `/pages/articleDetail?id=${e}`
			});
		},
	},
};
